import './App.css';
import LongTable from './LongTable';
import 'bootstrap/dist/css/bootstrap.min.css';
import IntroClassEntries from './introclass.json';
import DigitsEntries from './digits.json';
import ScrollTopArrow from './ScrollArrow';

function App() {
  const table1Title = "Table 1: Number of Patches for IntroClassJava (Excluding Digits)";
  const table2Title = "Table 2: Number of Patches for Digits";
  return (
    <div className="App">
      <header className="App-header">
        <h1>Supplementary Material</h1>
        <a className="App-link" href={`#introclass`}>{table1Title}</a>
        <a className="App-link" href={`#digits`}>{table2Title}</a>
      </header>
      <body className="bg-light">
        <ScrollTopArrow />
        <div className="container">
          <main>
            <div id="introclass">
              <LongTable 
                title={table1Title} 
                caption="Each cell shows the number of patches generated by GenProg / VarFix. The last column break down the number of minimized correct patches by the number of composing edits from 1 edit to 3 edits."
                entries={IntroClassEntries}
                useCorrect={true}/>
                <hr/>
            </div>
            <div id="digits">
              <LongTable 
                title={table2Title} 
                caption="Each cell shows the number of patches generated by GenProg / VarFix. The last column break down the number of minimized generalizable patches by the number of composing edits from 1 edit to 3 edits." 
                entries={DigitsEntries} 
                useCorrect={false} />
            </div>
          </main>
        </div>
      </body>
    </div>
  );
}

export default App;
