import React from 'react';
import Table from 'react-bootstrap/Table';

export default function LongTable({title, caption, entries, useCorrect}) {
    return(
        <div className="py-5 text-center">
            <h2>{title}</h2>
            <p className="lead">{caption}</p>
            <div className="tableFixHead">
                <Table striped bordered hover size="sm" >
                    <thead className="thead-dark fixHead">
                        <tr>
                            <th rowSpan={2} className="fixHead">Bug</th>
                            <th colSpan={4} className="fixHead">3 Edit Templates</th>
                            <th colSpan={4} className="fixHead">8 Edit Templates</th>
                        </tr>
                        <tr>
                            <th className="fixHead">Plausible</th>
                            <th className="fixHead">{useCorrect ? "Correct" : "Generalizable"}</th>
                            <th className="fixHead">Minimized Plausible</th>
                            <th className="fixHead">{useCorrect ? "Minimized Correct" : "Minimized Generalizable"}</th>
                            <th className="fixHead">Plausible</th>
                            <th className="fixHead">{useCorrect ? "Correct" : "Generalizable"}</th>
                            <th className="fixHead">Minimized Plausible</th>
                            <th className="fixHead">{useCorrect ? "Minimized Correct (Edits 1/2/3)" : "Minimized Generalizable (Edits 1/2/3)"}</th>
                        </tr>
                    </thead>
                    <tbody>
                        {entries.map((entry) => (
                            <TableEntry {...entry} />
                        ))}
                    </tbody>
                </Table>
            </div>
        </div>
    )
}

function TableEntry({ bug, pl3, crt3, mpl3, mcrt3, pl8, crt8, mpl8, mcrt8 }) {
    return (
        <tr>
            <td>{bug}</td>
            <td>{pl3}</td>
            <td>{crt3}</td>
            <td>{mpl3}</td>
            <td>{mcrt3}</td>
            <td>{pl8}</td>
            <td>{crt8}</td>
            <td>{mpl8}</td>
            <td>{mcrt8}</td>
        </tr>
    )
}
